@media (max-width: 768px) {
  .media-select {
    --outer-bound: 1rem;
  }
}
.media-select details {
  border: none;
  margin: 0;
  padding: 0;
}
.media-select summary::after {
  display: none;
}
.media-select li {
  margin-left: 2rem;
  position: relative;
}
.media-select li + li {
  margin-top: var(--spacing);
}
.media-select li > details > summary,
.media-select li > svg {
  position: absolute;
  left: -1rem;
  top: 0.75rem;
  transform: translateX(-100%);
}
.media-select svg path {
  stroke: currentColor !important;
}
.media-select ul,
.media-select li {
  list-style: none;
  margin-bottom: 0;
}
.media-select ul::after, .media-select ul::before, .media-select ul::marker,
.media-select li::after,
.media-select li::before,
.media-select li::marker {
  display: none;
}
.media-select label {
  border: var(--border-width) solid var(--primary);
  border-radius: var(--border-radius);
  display: inline-block;
  margin-bottom: 0;
  text-align: left;
  padding: var(--form-element-spacing-vertical) var(--form-element-spacing-horizontal);
  --background-color: transparent;
  --color: var(--primary);
}
.media-select label:is([aria-current], :hover, :active, :focus) {
  --background-color: transparent;
  --color: var(--primary-hover);
}
.media-select input[type=radio] {
  display: none;
}
.media-select input[type=radio]:checked + label {
  background-color: var(--primary);
  color: var(--primary-inverse);
}
.media-select input[type=radio]:disabled + label {
  color: var(--text-color);
  border-color: var(--text-color);
}
.media-select figure {
  display: flex;
  gap: var(--spacing);
  align-items: center;
}
.media-select .meida-image {
  max-width: 6rem;
}

body {
  padding-top: 2rem;
}

.layout-menu {
  background-color: grey;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 200;
  height: 2rem;
  box-sizing: border-box;
  padding: 0 1rem;
}
.layout-menu__button {
  display: inline-block;
  background-color: #4bddb1;
  font-weight: 800;
  color: #fff;
  text-decoration: none;
  border: none;
  padding: 0 1rem;
}
.layout-menu__button:focus, .layout-menu__button:hover {
  background-color: #26c595;
}
.layout-menu__button + * {
  margin-left: auto;
}
.layout-menu > * > * {
  margin-bottom: 0;
}

.layout-container, .layout-element, .layout-slot {
  width: 100%;
  position: relative;
}

.layout-container {
  border: solid 4px grey;
}

.layout-element {
  position: relative;
  border: solid 4px transparent;
  margin-top: -4px;
  margin-bottom: -4px;
  transition: 150ms border-color;
}
.layout-element:hover {
  border: solid 4px #4bddb1;
}
.layout-element:hover .layout-handle {
  opacity: 1;
}
.layout-element:hover .select-media {
  opacity: 1;
}
.layout-element .ce-block__content,
.layout-element .ce-toolbar__content {
  max-width: 100%;
}

.layout-slot {
  background: #f6f6f6;
  color: #333;
  border-radius: 0.5rem;
  padding: 1rem 1rem 1rem 4rem;
}

.layout-handle {
  opacity: 0;
  position: absolute;
  top: 0;
  right: 1rem;
  left: 1rem;
  transition: 150ms opacity;
  display: flex;
  gap: 2rem;
  justify-content: space-between;
}
.layout-handle__actions :first-child {
  border-bottom-left-radius: 0.5rem;
}
.layout-handle__actions :last-child {
  border-bottom-right-radius: 0.5rem;
}

