  color-scheme: light;
  --font-family: "Manrope", sans-serif;
  --line-height: 1.5;
  --font-weight: 400;
  --font-size: 1rem;
  --margin: 2rem;
  --spacing-100: 0.25rem;
  --spacing-200: 0.5rem;
  --spacing-300: 1rem;
  --spacing-400: 2rem;
  --spacing-500: 4rem;
  --spacing-600: 6rem;
  --spacing-700: 8rem;
  --spacing-800: 10rem;
  --spacing-900: 14rem;
  --border-width: 2px;
  --border-radius: 4px;
  --border-color: currentColor;
  --border-type: solid;
  --border: var(--border-color) var(--border-width) var(--border-type) var(--border-radius);
  --outline-width: 4px;
  --gutter: $spacing;
  --vertical-unit: 1.5rem;
  --outer-bound: 2rem;
  --inner-bound-width: 68rem;
  --outer-bound-width: 86rem;
  --header-height: clamp(5rem, (100vw * 0.0076923077) - -4.8461538462rem, 6.5rem);
  --screen-height: 100vh;
  --spacing: 1rem;
  --typography-spacing-vertical: 1.5rem;
  --block-spacing-vertical: 2rem;
  --block-spacing-horizontal: 1rem;
  --form-element-spacing-vertical: 0.75rem;
  --form-element-spacing-horizontal: 1rem;
  --grid-spacing-vertical: 0;
  --grid-spacing-horizontal: 1rem;
  --nav-element-spacing-vertical: 1rem;
  --nav-element-spacing-horizontal: 0.5rem;
  --nav-link-spacing-vertical: 0.5rem;
  --nav-link-spacing-horizontal: 0.5rem;
  --form-label-font-weight: var(--font-weight);
  --transition: 200ms ease-in-out;
  width: 100%;
  max-width: 52rem;
  margin: auto;
  border: none;
}
@media (max-width: 768px) {
  dialog {
    --outer-bound: 1rem;
  }
}
@media (min-width: 576px) {
  dialog {
    width: calc(100% - var(--outer-bound) * 2);
  }
}
dialog::backdrop {
  --modal-overlay-background-color: rgba(0, 0, 0, 0.2);
  background-color: var(--modal-overlay-background-color);
  --modal-overlay-backdrop-filter: blur(0.25rem);
  -webkit-backdrop-filter: var(--modal-overlay-backdrop-filter);
  backdrop-filter: var(--modal-overlay-backdrop-filter);
}
dialog .close,
dialog [aria-label=close] {
  position: sticky;
  display: block;
  right: 0;
  margin-left: auto;
  top: 0;
  width: 1rem;
  height: 1rem;
  background-image: var(--icon-close);
  background-position: center;
  background-size: auto 1rem;
  background-repeat: no-repeat;
  opacity: 0.5;
  padding: var(--form-element-spacing-vertical) var(--form-element-spacing-horizontal);
  margin-bottom: var(--spacing);
  --background-color: transparent;
  background-color: var(--background-color);
  --border-color: var(--contrast);
  border: var(--border-width) solid var(--border-color);
}
dialog .close:focus, dialog .close:hover,
dialog [aria-label=close]:focus,
dialog [aria-label=close]:hover {
  --box-shadow: var(--button-hover-box-shadow, 0 0 0 rgba(0, 0, 0, 0)), 0 0 0 var(--outline-width) var(--contrast-focus);
  --border-color: var(--contrast-hover);
  --color: var(--contrast-hover);
  --background-color: transparent;
  box-shadow: var(--box-shadow);
}
dialog > article {
  padding: var(--block-spacing-vertical) var(--block-spacing-horizontal);
  border-radius: var(--border-radius);
  background: var(--card-background-color);
  box-shadow: var(--card-box-shadow);
}
dialog > article > footer {
  border-top: var(--border-width) solid var(--card-border-color);
  border-bottom-right-radius: var(--border-radius);
  border-bottom-left-radius: var(--border-radius);
  border-top: var(--border-width) solid var(--card-border-color);
  border-bottom-right-radius: var(--border-radius);
  border-bottom-left-radius: var(--border-radius);
  padding: calc(var(--block-spacing-vertical) * 0.66) var(--block-spacing-horizontal);
  background-color: var(--card-sectionning-background-color);
  margin-top: var(--block-spacing-vertical);
  margin-bottom: calc(var(--block-spacing-vertical) * -1);
  margin-right: calc(var(--block-spacing-horizontal) * -1);
  margin-left: calc(var(--block-spacing-horizontal) * -1);
  gap: 0.5rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}
dialog > article main {
  min-height: 12rem;
  --border-color: var(--muted-color);
  border: var(--border-width) solid var(--border-color);
  border-radius: var(--border-radius);
  padding: var(--spacing) var(--form-element-spacing-horizontal);
}
.editor {
  background: #f6f6f6;
  color: #333;
  border-radius: 0.5rem;
  padding: 1rem 1rem 1rem 4rem;
}